@import url(https://fonts.googleapis.com/css?family=Open+Sans:400italic);
@import url(https://fonts.googleapis.com/css?family=Merriweather|Roboto+Slab|Rubik&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  pointer-events: none;
  margin-bottom : 50px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.bg-darker {
  background-color: #3161a3;
}

.App-link {
  color: #61dafb;
}

.margin-below { 

  margin-bottom : 10px;

}

.outlineButton {
  color : #fff;
  font-size : 15px;
  font-family : 'Rubik';
  border : none;
  padding : 5px;
  width : 100%;
  display : block;
  text-align : center;
}

.outlineButton :hover {
  text-decoration : none;
}

.custom-card{
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius : 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    -webkit-transition: box-shadow 83ms;
    transition: box-shadow 83ms;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: 83ms;
            transition-duration: 83ms;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
    padding : 0;
    padding-top : 23px;
    margin-bottom : 24px;
}

.card-title-wrapper {
  padding : 0 ;
  padding-left : 20px;
  padding-right : 20px;
}

.card-title-text {
 font-weight : 700;
 font-size : 30px;
}

