@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.post-wrapper {
    padding : 10px;
  }
  
  .post-wrapper p {
    color : #000;
    font-size: 19px;
    font-family: Georgia,Cambria,"Times New Roman",Times,serif;
    line-height: 1.6;
    display : block;
    margin-bottom : 20px;
  }
  
  .post-wrapper blockquote{
    font-size: 1.4em;
    width:100%;
    margin:50px auto;
    font-family:Open Sans;
    font-style:italic;
    color: #555555;
    padding:1.2em 30px 1.2em 75px;
    border-left:8px solid #78C0A8 ;
    line-height:1.6;
    position: relative;
    background:#EDEDED;
  }
  
  .post-wrapper blockquote::before{
    font-family:Arial;
    content: "\201C";
    color:#78C0A8;
    font-size:4em;
    position: absolute;
    left: 10px;
    top:-10px;
  }
  
  .post-wrapper blockquote::after{
    content: '';
  }

  .post-wrapper h1,h2,h3,h4 {
      color : #000;
      font-weight : bold;
      font-family:sans-serif;
  }
  
  .post-wrapper blockquote span{
    display:block;
    color:#333333;
    font-style: normal;
    font-weight: bold;
    margin-top:1em;
  }
  
  .buttonRow {
    padding : 10px;
    margin : 10px;
  }
  
  .padding {
    padding : 10px;
  }
  
  img:first-child {
   width : 100%;
   align-self: auto; 
  }

  h4:first-child {
    color : #333333; 
   }

.post-heading{
    font-size : 30px;
    line-height: 1.6;
    color: #000;
    font-family : "Segoe UI";
}
