@import url(https://fonts.googleapis.com/css?family=Open+Sans:400italic);
@import url('https://fonts.googleapis.com/css?family=Merriweather|Roboto+Slab|Rubik&display=swap');

.App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  pointer-events: none;
  margin-bottom : 50px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.bg-darker {
  background-color: #3161a3;
}

.App-link {
  color: #61dafb;
}

.margin-below { 

  margin-bottom : 10px;

}

.outlineButton {
  color : #fff;
  font-size : 15px;
  font-family : 'Rubik';
  border : none;
  padding : 5px;
  width : 100%;
  display : block;
  text-align : center;
}

.outlineButton :hover {
  text-decoration : none;
}

.custom-card{
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius : 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    transition: box-shadow 83ms;
    transition-property: box-shadow;
    transition-duration: 83ms;
    transition-timing-function: ease;
    transition-delay: 0s;
    padding : 0;
    padding-top : 23px;
    margin-bottom : 24px;
}

.card-title-wrapper {
  padding : 0 ;
  padding-left : 20px;
  padding-right : 20px;
}

.card-title-text {
 font-weight : 700;
 font-size : 30px;
}

.home-page-section {
   margin-bottom : 30px;
   margin-top : 30px;
   font-family : 'Rubik';
}

.primary-text {
  color : #0077b5;
}

.secondary-text{
  color : #EE5C5C;
}

.home-mini-text {
  font-size : 20px;
  font-family : source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.home-medium-text {
  font-size : 2em;
}

.home-mega-text {
  font-size : 4em;
}

.custom-card-img {
  height : 25%;
}

.full-button {
  background : #333;
  color : white;
  text-align: center;
  height : 40px;
  padding : 7px;
}

.full-button a{
  color : #fff;
  text-decoration : none;
}

.full-button a:hover{
  color : #fff;
  text-decoration : none;
}

.full-button-medium {
  background : #00ab6c;
  color : white;
  text-align: center;
  height : 40px;
  padding : 7px;
}

.full-button-medium a{
  color : #fff;
  text-decoration : none;
}

.full-button-medium a:hover{
  color : #fff;
  text-decoration : none;
}

.skills-container {
  /*border : 1px solid #e3e3e3;
  border-top : 5px solid #0077b5;*/
  margin : 20px;
}
.skills-container li {
  text-decoration: none;
  list-style: none;
}

.caption {
  color : rgba(0,0,0,.54);
}

.custom-button {
  background : transparent;
  text-align : center;
  height : 50px;
  padding : 10px;
  margin-top: 24px;
  border-radius : 3px;
  display : block;
}

.bg-gradient {
  background-image: radial-gradient( circle 587px at 49.8% 167.8%,  rgba(196,172,220,1) 0.1%, rgba(104,65,145,1) 100.2% );
}

.fa{
  font-size: 25px;
  text-align: center;
  text-decoration: none;
}

.fa-linkedin {
  color: #0077b5;
}

.fa-github {
  color: #333333;
}

.fa-medium {
  color: #00ab6c;
}

.fa-email {
  color: #24292e;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
